import type {
    AICommerceConfig,
    ChatRequest,
    ChatResponse,
    ChatContext,
    Session,
    APIError,
    Product,
} from './types';

/**
 * AI Commerce SDK Client
 * 
 * @example
 * ```typescript
 * import { AICommerce } from '@yassirbenmoussa/aicommerce-sdk';
 * 
 * const client = new AICommerce({ apiKey: 'your-api-key' });
 * const response = await client.chat('I need a laptop under $1000');
 * console.log(response.products);
 * ```
 */
export class AICommerce {
    private readonly apiKey: string;
    private readonly storeId: string | undefined;
    private readonly baseUrl: string;
    private readonly timeout: number;
    private sessionToken: string | null = null;

    constructor(config: AICommerceConfig) {
        if (!config.apiKey) {
            throw new Error('AICommerce: apiKey is required');
        }

        this.apiKey = config.apiKey;
        this.storeId = config.storeId;
        this.baseUrl = this.normalizeUrl(config.baseUrl || this.detectBaseUrl());
        this.timeout = config.timeout || 30000;
    }

    /**
     * Detect the base URL based on environment
     */
    private detectBaseUrl(): string {
        // Check for data attribute on script tag (set by widget loader)
        if (typeof window !== 'undefined') {
            const script = document.querySelector('script[data-aicommerce-url]');
            if (script) {
                return script.getAttribute('data-aicommerce-url') || 'https://api.aicommerce.dev';
            }
        }
        // Default to production API
        return 'https://api.aicommerce.dev';
    }

    /**
     * Normalize URL (remove trailing slash)
     */
    private normalizeUrl(url: string): string {
        return url.replace(/\/$/, '');
    }

    /**
     * Make an API request
     */
    private async request<T>(
        endpoint: string,
        options: RequestInit = {}
    ): Promise<T> {
        const url = `${this.baseUrl}${endpoint}`;

        const controller = new AbortController();
        const timeoutId = setTimeout(() => controller.abort(), this.timeout);

        try {
            const response = await fetch(url, {
                ...options,
                signal: controller.signal,
                headers: {
                    'Content-Type': 'application/json',
                    'x-api-key': this.apiKey,
                    ...(this.storeId && { 'x-store-id': this.storeId }),
                    ...(this.sessionToken && { 'X-Session-Token': this.sessionToken }),
                    ...options.headers,
                },
            });

            clearTimeout(timeoutId);

            if (!response.ok) {
                const errorData = await response.json().catch(() => ({}));
                const error: APIError = {
                    code: errorData.code || 'UNKNOWN_ERROR',
                    message: errorData.message || errorData.error || `HTTP ${response.status}`,
                    status: response.status,
                };
                throw new AICommerceError(error.message, error.code, error.status);
            }

            return response.json();
        } catch (error) {
            clearTimeout(timeoutId);

            if (error instanceof AICommerceError) {
                throw error;
            }

            if (error instanceof Error && error.name === 'AbortError') {
                throw new AICommerceError('Request timeout', 'TIMEOUT', 408);
            }

            throw new AICommerceError(
                error instanceof Error ? error.message : 'Unknown error',
                'NETWORK_ERROR',
                0
            );
        }
    }

    /**
     * Send a chat message and get product recommendations
     * 
     * @param message - The user's message or full ChatRequest object
     * @param context - Optional context for better recommendations
     * @returns Chat response with AI reply and products
     * 
     * @example
     * ```typescript
     * // Simple usage
     * const response = await client.chat('I need running shoes');
     * 
     * // With context
     * const response = await client.chat('I need running shoes', {
     *   budget: { max: 150 },
     *   preferences: ['comfortable', 'lightweight']
     * });
     * ```
     */
    async chat(
        message: string | ChatRequest,
        context?: ChatContext
    ): Promise<ChatResponse> {
        const request: ChatRequest = typeof message === 'string'
            ? { message, context, sessionToken: this.sessionToken || undefined }
            : { ...message, sessionToken: message.sessionToken || this.sessionToken || undefined };

        const response = await this.request<ChatResponse>('/api/v1/chat', {
            method: 'POST',
            body: JSON.stringify(request),
        });

        // Store session token for follow-up messages
        if (response.sessionToken) {
            this.sessionToken = response.sessionToken;
        }

        return response;
    }

    /**
     * Send an audio message and get product recommendations
     * 
     * @param audioBlob - Audio blob (from MediaRecorder or file input)
     * @param context - Optional context for better recommendations
     * @returns Chat response with AI reply and products
     * 
     * @example
     * ```typescript
     * // Record audio using MediaRecorder
     * const mediaRecorder = new MediaRecorder(stream);
     * const chunks: Blob[] = [];
     * mediaRecorder.ondataavailable = (e) => chunks.push(e.data);
     * mediaRecorder.onstop = async () => {
     *   const audioBlob = new Blob(chunks, { type: 'audio/webm' });
     *   const response = await client.chatWithAudio(audioBlob);
     *   console.log(response.reply);
     * };
     * ```
     */
    async chatWithAudio(
        audioBlob: Blob,
        context?: ChatContext
    ): Promise<ChatResponse> {
        // Convert blob to base64
        const arrayBuffer = await audioBlob.arrayBuffer();
        const base64 = btoa(
            new Uint8Array(arrayBuffer).reduce(
                (data, byte) => data + String.fromCharCode(byte),
                ''
            )
        );

        const request: ChatRequest = {
            audioBase64: base64,
            audioMimeType: audioBlob.type || 'audio/webm',
            context,
            sessionToken: this.sessionToken || undefined,
        };

        const response = await this.request<ChatResponse>('/api/v1/chat', {
            method: 'POST',
            body: JSON.stringify(request),
        });

        // Store session token for follow-up messages
        if (response.sessionToken) {
            this.sessionToken = response.sessionToken;
        }

        return response;
    }

    /**
     * Create a new chat session
     * 
     * @returns Session information with token
     */
    async createSession(): Promise<Session> {
        const response = await this.request<{ session: Session }>('/api/v1/chat/session', {
            method: 'POST',
        });

        this.sessionToken = response.session.token;
        return response.session;
    }

    /**
     * Clear the current session
     */
    clearSession(): void {
        this.sessionToken = null;
    }

    /**
     * Get the current session token
     */
    getSessionToken(): string | null {
        return this.sessionToken;
    }

    /**
     * Set a session token (for restoring sessions)
     */
    setSessionToken(token: string): void {
        this.sessionToken = token;
    }

    // ============================================
    // Products API
    // ============================================

    /**
     * Products API namespace
     */
    readonly products = {
        /**
         * Create a new product
         */
        create: async (product: CreateProductInput): Promise<ProductResponse> => {
            return this.request<ProductResponse>('/api/v1/products', {
                method: 'POST',
                body: JSON.stringify(product),
            });
        },

        /**
         * Batch upsert products (create or update)
         */
        batchUpsert: async (products: CreateProductInput[]): Promise<BatchUpsertResponse> => {
            return this.request<BatchUpsertResponse>('/api/v1/products', {
                method: 'POST',
                body: JSON.stringify({ products }),
            });
        },

        /**
         * List products with pagination
         */
        list: async (options?: ListProductsOptions): Promise<ListProductsResponse> => {
            const params = new URLSearchParams();
            if (options?.page) params.set('page', String(options.page));
            if (options?.perPage) params.set('perPage', String(options.perPage));
            if (options?.search) params.set('search', options.search);
            if (options?.categoryId) params.set('categoryId', options.categoryId);
            if (options?.isActive !== undefined) params.set('isActive', String(options.isActive));

            const query = params.toString();
            return this.request<ListProductsResponse>(`/api/v1/products${query ? `?${query}` : ''}`);
        },

        /**
         * Get a single product by ID
         */
        get: async (productId: string): Promise<ProductResponse> => {
            return this.request<ProductResponse>(`/api/v1/products/${productId}`);
        },

        /**
         * Update a product
         */
        update: async (productId: string, data: UpdateProductInput): Promise<ProductResponse> => {
            return this.request<ProductResponse>(`/api/v1/products/${productId}`, {
                method: 'PUT',
                body: JSON.stringify(data),
            });
        },

        /**
         * Delete a product
         */
        delete: async (productId: string): Promise<{ success: boolean; deleted: boolean }> => {
            return this.request<{ success: boolean; deleted: boolean }>(`/api/v1/products/${productId}`, {
                method: 'DELETE',
            });
        },
    };

    // ============================================
    // Upload API
    // ============================================

    /**
     * Upload an image file
     * 
     * @example
     * ```typescript
     * // Upload from File input
     * const file = document.querySelector('input[type="file"]').files[0];
     * const result = await client.upload(file);
     * console.log(result.url);
     * 
     * // Upload and associate with product
     * const result = await client.upload(file, { productId: 'prod_123', isPrimary: true });
     * ```
     */
    async upload(file: File | Blob, options?: UploadOptions): Promise<UploadResponse> {
        const formData = new FormData();
        formData.append('file', file);

        if (options?.folder) formData.append('folder', options.folder);
        if (options?.productId) formData.append('productId', options.productId);
        if (options?.isPrimary) formData.append('isPrimary', 'true');

        const url = `${this.baseUrl}/api/v1/upload`;

        const response = await fetch(url, {
            method: 'POST',
            headers: {
                'X-API-Key': this.apiKey,
            },
            body: formData,
        });

        if (!response.ok) {
            const errorData = await response.json().catch(() => ({}));
            throw new AICommerceError(
                errorData.message || errorData.error || `HTTP ${response.status}`,
                errorData.code || 'UPLOAD_ERROR',
                response.status
            );
        }

        return response.json();
    }

    /**
     * Static method for one-off chat requests
     * 
     * @example
     * ```typescript
     * const response = await AICommerce.quickChat({
     *   apiKey: 'your-api-key',
     *   message: 'I need a laptop'
     * });
     * ```
     */
    static async quickChat(options: {
        apiKey: string;
        message: string;
        baseUrl?: string;
        context?: ChatContext;
    }): Promise<ChatResponse> {
        const client = new AICommerce({
            apiKey: options.apiKey,
            baseUrl: options.baseUrl,
        });
        return client.chat(options.message, options.context);
    }
}

// ============================================
// Product Types
// ============================================

export interface CreateProductInput {
    name: string;
    slug: string;
    description?: string;
    sku?: string;
    barcode?: string;
    price: number;
    compareAtPrice?: number;
    currency?: string;
    quantity?: number;
    trackInventory?: boolean;
    isActive?: boolean;
    isFeatured?: boolean;
    tags?: string;
    externalId?: string;
    categoryId?: string;
    images?: Array<{ url: string; alt?: string; isPrimary?: boolean }>;
}

export interface UpdateProductInput {
    name?: string;
    slug?: string;
    description?: string | null;
    sku?: string | null;
    barcode?: string | null;
    price?: number;
    compareAtPrice?: number | null;
    currency?: string;
    quantity?: number;
    trackInventory?: boolean;
    isActive?: boolean;
    isFeatured?: boolean;
    tags?: string | null;
    categoryId?: string | null;
}

export interface ProductResponse {
    success: boolean;
    product: Product;
}

export interface BatchUpsertResponse {
    success: boolean;
    processed: number;
    errors: number;
    results: Array<{ id: string; slug: string; status: 'created' | 'updated' }>;
    errorDetails?: Array<{ slug: string; error: string }>;
}

export interface ListProductsOptions {
    page?: number;
    perPage?: number;
    search?: string;
    categoryId?: string;
    isActive?: boolean;
}

export interface ListProductsResponse {
    success: boolean;
    data: Product[];
    total: number;
    page: number;
    perPage: number;
    totalPages: number;
}

export interface UploadOptions {
    folder?: string;
    productId?: string;
    isPrimary?: boolean;
}

export interface UploadResponse {
    success: boolean;
    url: string;
    key: string;
    size: number;
    contentType: string;
    productImage?: {
        id: string;
        url: string;
        alt: string | null;
        isPrimary: boolean;
    };
}

/**
 * Custom error class for AI Commerce SDK
 */
export class AICommerceError extends Error {
    readonly code: string;
    readonly status: number;

    constructor(message: string, code: string, status: number) {
        super(message);
        this.name = 'AICommerceError';
        this.code = code;
        this.status = status;

        // Maintain proper prototype chain
        Object.setPrototypeOf(this, AICommerceError.prototype);
    }
}
